

import TabItem from 'vux/src/components/tab/tab-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

import { JDGetQRCodeView } from '@/api/getData'
import { mapState } from 'vuex'
export default {
data() {
return {
value: 0,
show1: false,
text1: 'loading',
result: ''
};
},
computed: {
...mapState({
wxOpenId: state => state.mutations.wxOpenId
})
},
components: {
Tab,
TabItem,
Loading,
Icon
},
mounted() {
this.switchTabItem(0)
},
methods: {
switchTabItem (index) {
console.log('on-before-index-change', index)
this.show1 = true
setTimeout(() => {
this.show1 = false
this.value = index
this.getInfo(index + 1)
}, 1000)
},
async getInfo(value) {
let data = await JDGetQRCodeView({FType: value, FWeixinOpenID: this.wxOpenId})
console.log(data)
this.result = eval(data)
}
}
};



